}

/* Text Colors */
.yellow{
  color: #ffde02;
}

/* Flexbox Alignment */
.header-child-section.al-flex-start {
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.header-child-section.al-flex-center {
  -webkit-align-items: center;
          align-items: center;
}

/* Flexbox Flex Basis */
.flx-bas-30 {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
}

.flx-bas-40 {
  -webkit-flex-basis: 40%;
          flex-basis: 40%;
}

.flx-bas-50 {
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
}

.flx-bas-60 {
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
}

.flx-bas-70 {
  -webkit-flex-basis: 70%;
          flex-basis: 70%;
}

/* Text Align */
.align-center {
  text-align: center;
}

.rep-overlay {
  position: relative;
  margin-bottom: 70px;
  z-index: 1;
}

.rep-overlay:after {
  content: attr(data-rep-overlay);
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  -webkit-transform: scale(2) translate(30%, 0%);
          transform: scale(2) translate(30%, 0%);
  opacity: 0.3;
  -webkit-filter: blur(2px);
          filter: blur(2px);
  -webkit-animation: sway 10s linear 2s infinite alternate;
          animation: sway 10s linear 2s infinite alternate;
  color: #ffde02a6;
  z-index: -1;
}

@-webkit-keyframes sway {
  from {
    -webkit-transform: scale(2) translate(30%, 0%);
            transform: scale(2) translate(30%, 0%);
  }
  to{
    -webkit-transform: scale(2) translate(30%, 20%);
            transform: scale(2) translate(30%, 20%);
  }
}

@keyframes sway {
  from {
    -webkit-transform: scale(2) translate(30%, 0%);
            transform: scale(2) translate(30%, 0%);
  }
  to{
    -webkit-transform: scale(2) translate(30%, 20%);
            transform: scale(2) translate(30%, 20%);
  }
}

@-webkit-keyframes sway2 {
from {
  -webkit-transform: scale(2) translate(-10px, 0%);
          transform: scale(2) translate(-10px, 0%);
}
to{
  -webkit-transform: scale(2) translate(10px, 0%);
          transform: scale(2) translate(10px, 0%);
}
}

@keyframes sway2 {
from {
  -webkit-transform: scale(2) translate(-10px, 0%);
          transform: scale(2) translate(-10px, 0%);
}
to{
  -webkit-transform: scale(2) translate(10px, 0%);
          transform: scale(2) translate(10px, 0%);
}
}

@media screen and (max-width: 960px){
  .small{
    font-size: 14px;
  }
  
  .large {
    font-size: 40px;
  }
}

@media screen and (max-width: 767px){
  .App-header {
    right: 10%;
    min-width: 150px;
  }

  .animate .main {
    -webkit-transform: translateZ(-400px) translateX(-50%) rotateY(45deg);
            transform: translateZ(-400px) translateX(-50%) rotateY(45deg);
  }

  .MenuIcon__wrapper {
    right: 20px;
    top: 20px;
  }
}

.NavItem__wrapper {
  opacity: 0;
  -webkit-transform: translateX(350px) translateZ(-1000px);
          transform: translateX(350px) translateZ(-1000px);
  transition: opacity 0.4s, -webkit-transform 0.4s;
  transition: transform 0.4s, opacity 0.4s;
  transition: transform 0.4s, opacity 0.4s, -webkit-transform 0.4s;
}

.NavItem__wrapper:nth-child(2) {
  transition-delay: 0.04s;
}
.NavItem__wrapper:nth-child(3) {
  transition-delay: 0.08s;
}
.NavItem__wrapper:nth-child(4) {
  transition-delay: 0.12s;
}
.NavItem__wrapper:nth-child(5) {
  transition-delay: 0.16s;
}

.animate .NavItem__wrapper {
  -webkit-transform: translateX(0) translateZ(0);
          transform: translateX(0) translateZ(0);
  opacity: 1;
}

.NavItem {
  font-size: 2rem;
  color: #fff;
  display: inline-block;
  text-decoration: none;
  margin: 10px 0;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 1;
  position: relative;
  transition: all 0.4s;
}

.NavItem:hover {
  color: #ffde02;
}

.NavItem:before {
  content: '';
  width: 0;
  height: 2px;
  display: inline-block;
  background-color: #ffde02;
  vertical-align: middle;
  transition: all 0.4s;
}

.NavItem:hover:before {
  width: 30px;
  margin-right: 10px;
}

@media screen and (max-width:767px){
    .NavItem {
      font-size: 1.5rem;
    }

    .NavItem:hover:before {
      width: 10px;
      margin-right: 5px;
    }
}
.container{
  height: 100vh;
  overflow: auto;
  scroll-behavior: smooth;
}

.animate .container{
  overflow: hidden;
}

.animate .container:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255,255,255,0.5);
}

.main .container::-webkit-scrollbar {
  width: 5px;
}
  
.main .container::-webkit-scrollbar-track {
  background-color: #000;
}
  
.main .container::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #000;
  box-shadow: inset 0 0 2px #ffde02;
}

.main .container::-webkit-scrollbar-thumb:hover{
  background: linear-gradient(#ffde00, #e74300);
}
